.contact-us{
    margin: 100px 0;
    @media (max-width: 767px) {
        margin: 50px 0;
      }
    .buttons{
        margin: 60px 0;
        display: grid;
        grid-template-columns: 1fr 1fr;
        @media (max-width: 1024px) {
            margin: 30px 0;
          }
          @media (max-width: 767px) {
            grid-template-columns: 1fr;
          }
        a{
            padding: 40px;
            background: linear-gradient(97deg,
             #C444FF 0.62%, #562999 100%);
            font-size: 48px;
            color: white;
            font-weight: normal;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width: 1200px) {
                font-size: 40px;
                padding: 30px;
              }
              @media (max-width: 1024px) {
                font-size: 30px;
                padding: 25px;
              }
              @media (max-width: 767px) {
                font-size: 22px;
                margin-bottom: 15px;
                padding: 20px;
              }
            img{
                margin-left: 20px;
                @media (max-width: 1024px) {
                    width: 30px;
                  }
                  @media (max-width: 767px) {
                    width: 26px;
                  }
            }
        }
    }
    .contact-info{
        display: flex;
        justify-content: space-between;
        margin-top: 100px;
        @media (max-width: 1200px) {
            margin-top: 60px;
          }
          @media (max-width: 1024px) {
            flex-direction: column;
            margin-top: 50px;
          }
        .info{
            display: flex;
            flex-direction: column;
            p{
                align-items: center;
                display: flex;
                color: white;
                font-size: 28px;
                margin-bottom: 30px;
                @media (max-width: 1200px) {
                    font-size: 24px;
                  }
                  @media (max-width: 767px) {
                    font-size: 18px;
                    margin-bottom: 15px;
                  }
                img{
                    margin-right: 20px;
                    @media (max-width: 767px) {
                      margin-right: 10px;
                      width: 40px;
                      }
                }
            }
            .text{
                align-items: center;
                display: flex;
                color: white;
                font-size: 28px;
                margin-bottom: 30px;
                @media (max-width: 1200px) {
                    font-size: 24px;
                  }
                  @media (max-width: 767px) {
                    font-size: 18px;
                    margin-bottom: 15px;
                  }
                img{
                    margin-right: 20px;
                    @media (max-width: 767px) {
                        margin-right: 10px;
                        width: 40px;
                        }
                }
            }
            .btn{
                display: flex;
                align-items: center;
                align-self: flex-start;
                margin-top: auto;
                padding: 24px;
                font-size: 28px;
                color: white;
                padding-right: 50px;
                background: linear-gradient(97deg, #C444FF 0.62%,
                 #562999 120%);
                 @media (max-width: 767px) {
                    font-size: 24px;
                    padding: 16px;
                    width: 100%;
                  }
                img{
                    margin-right: 30px;
                }
            }
        }
        #map{
            max-width: 640px;
            width: 100%;
            height: 470px;
            overflow: hidden;
            border-radius: 50px;
            margin-left: 50px;
            @media (max-width: 1200px) {
               max-width: 500px;
               margin-left: 30px;
              }
              @media (max-width: 1024px) {
                max-width: 100%;
                margin-left: 0;
                margin-top: 30px;
              }
              @media (max-width: 767px) {
               height: 70vw;
               border-radius: 20px;
              }
        }
    }
}